import styled from 'styled-components';

export const Container = styled.div`
  width: 80%;
  margin: 0 auto;
  padding: 40px;
  background-color: ${({ theme }) => theme.invoiceBg};
  border-radius: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;

  @media screen and (max-width: 768px) {
    padding: 20px;
    width: 90%;
  }

  @media (max-width: 480px) {
    padding: 20px;
  }
`;

export const Title = styled.h2`
  font-size: 2rem;
  font-weight: bold;
  color: ${({ theme }) => theme.invoiceTextColor};
  text-align: center;
  margin-bottom: 20px;

  @media screen and (max-width: 768px) {
    font-size: 1.8rem;
  }

  @media (max-width: 480px) {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
`;

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  gap: 30px;

  @media screen and (max-width: 768px) {
    gap: 20px;
  }

  @media (max-width: 480px) {
    gap: 20px;
  }
`;

export const FormRow = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

export const FormLabel = styled.label`
  font-weight: bold;
  font-size: 1rem;
  color: ${({ theme }) => theme.invoiceTextColor};


  @media screen and (max-width: 768px) {
    font-size: 0.9rem;
  }

  @media (max-width: 480px) {
    font-size: .8rem;
  }
`;

export const Input = styled.input`
  padding: 12px;
  border: 2px solid ${({ theme }) => theme.invoiceAccentColor};
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;


  &:focus {
    outline: none;
    border-color: ${({ theme }) => theme.invoiceAccentColor};
    box-shadow: 0 0 0 2px ${({ theme }) => theme.invoiceAccentColor}40;
  }

  @media screen and (max-width: 768px) {
    padding: 10px;
    font-size: 0.9rem;
  }

  @media (max-width: 480px) {
    padding: 10px;
    font-size: .8rem;
  }
`;

export const Button = styled.button`
  background-color: ${({ theme }) => theme.invoiceButtonColor};
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3); /* Adding text shadow for 3D effect */

  &:hover, &:focus {
    background-color: ${({ theme }) => theme.invoicebuttonHoverBg};
    outline: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

  &:active {
    transform: translateY(2px);
  }

  @media screen and (max-width: 768px) {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  @media (max-width: 480px) {
    padding: 8px 16px;
    font-size: 0.7rem;
  }
`;

export const ServicesList = styled.ul`
  margin-top: 30px;
  padding: 0;
  list-style-type: none;
`;

export const ServiceItem = styled.li`
  padding: 20px;
  border: 2px solid #ccc;
  border-radius: 12px;
  margin-bottom: 20px;
  font-size: 1rem;

  @media screen and (max-width: 768px) {
    padding: 15px;
  }

  @media (max-width: 480px) {
    padding: 15px;
    font-size: .8rem;
  }
`;

export const TotalAmount = styled.p`
  text-align: center;
  margin-top: 30px;
  font-size: 1.2rem;
  color: ${({ theme }) => theme.invoiceTextColor};

  @media screen and (max-width: 768px) {
    margin-top: 20px;
    font-size: 1rem;
  }

  @media (max-width: 480px) {
    margin-top: 20px;
    font-size: 0.9rem;
  }
`;

export const InputContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
`;

export const LogoContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;

  @media screen and (max-width: 768px) {
    gap: 10px;
  }
`;

export const CompanyLogo = styled.img`
  max-width: 200px;

  @media screen and (max-width: 768px) {
    max-width: 150px;
  }

  @media screen and (max-width: 480px) {
    max-width: 100px;
  }
`;

export const CompanyDetails = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 10px;
  margin-bottom: 20px;
  width: 100%;

  & > * {
    margin-bottom: 10px;
  }

  @media screen and (max-width: 768px) {
    gap: 10px;
  }
`;

export const CompanyDetailsInput = styled.input`
  padding: 10px;
  border: 2px solid ${({ theme }) => theme.invoiceAccentColor};
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  width: 100%;

  &:focus {
    outline: none;
    border-color: ${({ theme }) => theme.invoiceAccentColor};
    box-shadow: 0 0 0 2px ${({ theme }) => theme.invoiceAccentColor}40;
  }

  @media screen and (max-width: 768px) {
    font-size: 0.9rem;
  }

  @media screen and (max-width: 480px) {
    font-size: 0.8rem;
  }
`;

export const RemoveButton = styled.button`
  background-color: ${({ theme }) => theme.removeButtonBg || '#ff4d4d'};
  color: ${({ theme }) => theme.removeButtonText || '#fff'};
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  margin-left: 10px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: ${({ theme }) => theme.removeButtonHoverBg || '#ff3333'};
  }

  &:focus {
    outline: none;
    box-shadow: 0 0 0 3px ${({ theme }) => theme.removeButtonFocusShadow || 'rgba(255, 77, 77, 0.5)'};
  }

  &:active {
    transform: translateY(1px);
  }

  @media screen and (max-width: 768px) {
    padding: 4px 8px;
    font-size: 0.7rem;
  }

  @media (max-width: 480px) {
    padding: 2px 6px;
    font-size: 0.6rem;
  }
`;

export const FileInputContainer = styled.div`
  border: 2px dashed ${({ theme }) => theme.invoiceAccentColor};
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  cursor: pointer;
  transition: border-color 0.3s ease;
  background-color: ${({ theme }) => theme.inputBg || '#fff'};

  &:hover {
    border-color: ${({ theme }) => theme.invoiceAccentColor};
  }

  @media screen and (max-width: 768px) {
    padding: 10px;
  }

  @media (max-width: 480px) {
    padding: 8px;
  }
`;

export const FileInput = styled.input`
  opacity: 0;
  width: 0.1px;
  height: 0.1px;
  position: absolute;
`;

export const FileInputLabel = styled.label`
  font-weight: bold;
  font-size: 1rem;
  color: ${({ theme }) => theme.invoiceTextColor || '#000'};
  padding: 12px 24px;
  border-radius: 8px;
  background-color: ${({ theme }) => theme.invoiceButtonColor || '#007bff'};
  color: ${({ theme }) => theme.buttonText || '#fff'};
  transition: background-color 0.3s ease;
  display: inline-block;

  &:hover {
    background-color: ${({ theme }) => theme.buttonHoverBg || '#0056b3'};
  }

  @media screen and (max-width: 768px) {
    font-size: 0.9rem;
    padding: 10px 20px;
  }

  @media (max-width: 480px) {
    font-size: 0.8rem;
    padding: 8px 16px;
  }
`;

export const QuickButtonsRow = styled.div`
  display: flex;
  gap: 10px;
  align-items: center;
`;

export const CurrencySelector = styled.select`
  padding: 12px;
  border: 2px solid ${({ theme }) => theme.invoiceAccentColor};
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3); /* Adding text shadow for 3D effect */

  &:focus {
    outline: none;
    border-color: ${({ theme }) => theme.invoiceAccentColor};
    box-shadow: 0 0 0 2px ${({ theme }) => theme.invoiceAccentColor}40;
  }

  @media screen and (max-width: 768px) {
    padding: 10px;
    font-size: 0.9rem;
  }

  @media (max-width: 480px) {
    padding: 10px;
    font-size: .8rem;
  }
`;

export const placeholderLogo = styled.img`
  max-width: 200px;

  @media screen and (max-width: 768px) {
    max-width: 150px;
  }

  @media screen and (max-width: 480px) {
    max-width: 100px;
  }
`;
