import React from 'react';
import styled from 'styled-components';
import UTCClock from './UTCClock';
import ThemeToggle from './ThemeToggle'; // Import ThemeToggle component

const NavbarContainer = styled.nav`
  display: flex;
  justify-content: space-between; /* Adjust alignment for responsive layout */
  align-items: center;
  padding: 16px 20px;
  background-color: ${({ theme }) => theme.navBg};
  color: ${({ theme }) => theme.navTextColor};
  box-shadow: ${({ theme }) => theme.boxShadow};
  margin-bottom: 40px;

  border-radius: 10px;
`;

const UTCClockContainer = styled.div`
  font-size: 18px;
  font-weight: bold;
  color: ${({ theme }) => theme.clockColor};
  
`;

const NavBar = ({ toggleTheme }) => { // Receive toggleTheme as a prop
  return (
    <NavbarContainer>
      <UTCClockContainer>
        <UTCClock />
      </UTCClockContainer>
      <ThemeToggle toggleTheme={toggleTheme} /> {/* Pass toggleTheme to ThemeToggle */}
    </NavbarContainer>
  );
};

export default NavBar;
