import React from 'react';
import styled from 'styled-components';
import { FiSun, FiMoon } from 'react-icons/fi'; // Import sun and moon icons

const ToggleButton = styled.div`
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: ${({ theme }) => theme.buttonBg};
  transition: background-color 0.3s ease;
  box-shadow: ${({ theme }) => theme.boxShadow};

  svg {
    fill: ${({ theme }) => theme.buttonBg}; /* Icon color */
    transition: fill 0.3s ease;
    width: 24px;
    height: 24px;
    font-weight: bold;
  }

  &:hover {
    background-color: ${({ theme }) => theme.buttonHoverBg};
    svg {
      fill: ${({ theme }) => theme.iconHoverColor}; /* Icon hover color */
    }
  }

  @media (max-width: 768px) {
    /* Adjust size and spacing for smaller screens */
    width: 32px;
    height: 32px;
    svg {
      width: 20px; /* Decrease the size of the SVG icon for smaller screens */
      height: 20px; /* Decrease the size of the SVG icon for smaller screens */
    }
    margin-right: 10px; /* Add spacing between the button and other elements */
  }

  @media (max-width: 480px) {       
    width: 28px;
    height: 28px;
    svg {
      width: 18px; /* Further decrease the size of the SVG icon for smaller screens */
      height: 18px; /* Further decrease the size of the SVG icon for smaller screens */
    }
    margin-right: 10px; /* Add spacing between the button and other elements */
  }
`;

const ThemeToggle = ({ isLightMode, toggleTheme }) => {
  const handleToggle = () => {
    toggleTheme(); // Call the toggleTheme function passed from parent
  };

  return (
    <ToggleButton onClick={handleToggle}>
      {isLightMode ? <FiMoon size={24} fill="#ffffff" /> : <FiSun size={24} fill="#000000" />}
    </ToggleButton>
  );
};

export default ThemeToggle;
