import React from 'react';
import styled from 'styled-components';

// Import images
import HEXscout from './images/HEXscout.jpg';
import gopulse from './images/gopulse.jpg';
import Trezor from './images/Trezor.jpg';
import scan from './images/scan.jpg';
import bridge from './images/bridge.jpg';
import pulsex from './images/pulsex.jpg';
import icosa from './images/icosa.jpg';
import gorealdefi from './images/gorealdefi.jpg';
import launch from './images/launch.jpg';
import RHyoutube from './images/RHyoutube.jpg';
import z from './images/z.jpg';
import richardHEART from './images/richardHEART.jpg';
import HEXstats from './images/HEXstats.jpg';
import apphexwin from './images/apphexwin.jpg';
import RHpattern from './images/RHpattern.jpg';
import hexPINK from './images/hexPINK.jpg';
import stakeHEXtoday from './images/stakeHEXtoday.jpg';
import HEXfire from './images/HEXfire.jpg';
import hextoys from './images/hextoys.jpg';
import dexTOYS from './images/dexTOYS.jpg';
import HEXcrypto from './images/HEXcrypto.jpg';
import maximus from './images/maximus.jpg';
import internetmoney from './images/internetmoney.jpg';
import timepaysme from './images/timepaysme.jpg';
import piteas from './images/piteas.jpg';
import thepulsetube from './images/thepulsetube.jpg';
import HEXpub from './images/HEXpub.jpg';
import hexdotcom from './images/hexdotcom.jpg';
import pulsechain from './images/pulsechain.jpg';
import pulsex1 from './images/pulsex(1).jpg';
import image3A25633_Glitch12 from './images/image3A25633_Glitch12.jpg';
import hexchart from './images/hexchart.jpg';


const CardGridContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 40px;
  width: 100%;
  margin-bottom: 40px;

  @media (max-width: 768px) {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  }

  @media (max-width: 480px) {
    grid-template-columns: repeat(auto-fill, minmax(60px, 1fr));
    max-width: 400px; 
    margin-bottom: 20px;
  }
`;

const Card = styled.div`
  background-color: ${({ theme }) => theme.bodyBg};
  border-radius: 20px; /* Adjust the border-radius as needed */

  transition: transform 0.3s ease, box-shadow 0.3s ease;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
`;

const CardImage = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 20px; /* Adjust the border-radius as needed */
  object-fit: cover; /* or object-fit: contain; */
`;

const CardLabel = styled.div`
  text-align: center;
  color: ${({ theme }) => theme.textColor};
  font-size: 16px;
  margin-top: 10px;
  font-weight: 500;

  @media (max-width: 768px) {
    font-size: 14px;
  }

  @media (max-width: 480px) {
    font-size: 12px;
  }
`;

const CardLink = styled.a`
  text-decoration: none;
  color: ${({ theme }) => theme.linkColor};
  font-size: 14px;
  margin-top: 5px;

  &:hover {
    text-decoration: underline;
  }
`;

const CardGrid = ({ theme }) => {
  const cardItems = [
    { path: HEXscout, link: 'https://hexscout.com/market', label: 'HEXscout ' },
    { path: gopulse, link: 'https://gopulse.com/', label: 'GoPulse' },
    { path: Trezor, link: 'https://go.hex.com/', label: 'GoHEXcom' },
    { path: scan, link: 'https://scan.pulsechain.com/', label: 'Scan' },
    { path: bridge, link: 'https://bridge.pulsechain.com/', label: 'Bridge' },
    { path: launch, link: 'https://launchpad.pulsechain.com/', label: 'LaunchPad' },
    { path: pulsex, link: 'https://app.pulsex.com/', label: 'PulseX' },
    { path: icosa, link: 'https://app.icosa.pro/', label: 'IcosaApp' },
    { path: gorealdefi, link: 'https://gorealdefi.com/', label: 'GoRealDefi' },
    { path: RHyoutube, link: 'https://www.youtube.com/RichardHeart', label: 'RHYouTube' },
    { path: z, link: 'https://hexsearch.io/', label: 'RHSearch' },
    { path: richardHEART, link: 'https://twitter.com/RichardHeartWin', label: 'RHTwitter' },
    { path: HEXstats, link: 'https://hexstats.today/', label: 'HEXStats' },
    { path: apphexwin, link: 'https://apphex.win/charts/', label: 'AppHEXWin' },
    { path: RHpattern, link: 'https://hexcalc.net/', label: 'HEXCalc' },
    { path: hexPINK, link: 'https://hex.pink/', label: 'HEXPink' },
    { path: stakeHEXtoday, link: 'https://stakehex.today/', label: 'StakeHEXToday' },
    { path: HEXfire, link: 'http://hexfire.io/', label: 'HEXFire' },
    { path: hextoys, link: 'https://store.hex.toys/', label: 'HEXToys' },
    { path: dexTOYS, link: 'https://www.dex.toys/swap', label: 'Dex Toys' },
    { path: HEXcrypto, link: 'https://www.hexcrypto.com/', label: 'HEXCrypto' },
    { path: maximus, link: 'https://bento.me/maximusdao', label: 'MaximusDao' },
    { path: internetmoney, link: 'https://internetmoney.io/', label: 'InternetMoney' },
    { path: timepaysme, link: 'https://timepays.me/', label: 'TimePaysMe' },
    { path: piteas, link: 'https://piteas.io/', label: 'Piteas' },
    { path: thepulsetube, link: 'https://thepulsetube.com/hex-crypto-videos/', label: 'PulseTube' },
    { path: HEXpub, link: 'https://hex.pub/', label: 'HEXPub' },
    { path: hexdotcom, link: 'https://hex.com/', label: 'HEXcom' },
    { path: pulsechain, link: 'https://pulsechain.com/', label: 'PulseChain' },
    { path: pulsex1, link: 'https://pulsex.com/', label: 'PulseX' },
    { path: image3A25633_Glitch12, link: 'https://docs.google.com/document/d/112IkMxL94d5AlXukQonRLlyNlF0_5zmX6k1Z0Stsmo0/edit#heading=h.qhzflfy2nxgz', label: 'DeepDive' },
    { path: hexchart, link: 'https://hexchart.com/', label: 'HEXChart' },
    // Add more images and links as needed
  ];

  return (
    <CardGridContainer>
      {cardItems.map((item, index) => (
        <CardLink key={index} href={item.link} target="_blank" rel="noopener noreferrer">
          <Card>
            <CardImage src={item.path} alt={item.label} />
            <CardLabel>{item.label.toUpperCase()}</CardLabel>
          </Card>
        </CardLink>
      ))}
    </CardGridContainer>
  );
};

export default CardGrid;