import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { getLiveData } from './Api';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTwitter } from '@fortawesome/free-brands-svg-icons';
import hexplslogo from './images/hexplslogo.png';
import hexethlogo from './images/hexethlogo.png';
import plslogo from './images/plslogo.png';
import plsxlogo from './images/plsxlogo.png';
import inclogo from './images/inclogo.png';

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 40px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  @media (max-width: 768px) {
    font-size: 14px;
  }

  @media (max-width: 480px) {
    font-size: 14px;
  }
`;

const TableRow = styled.tr`
  &:nth-child(even) {
    
  }

  &:hover {
    transform: scale(1.01);
  }
`;

const TableHeader = styled.th`
  padding: 20px 10px;
  background-color: ${({ theme }) => theme.tableHeaderBg};
  color: ${({ theme }) => theme.titleColor};
  font-weight: bold;
  text-align: center;
  border-bottom: 2px solid ${({ theme }) => theme.tableBorderColor};
  border-right: 1px solid ${({ theme }) => theme.tableBorderColor};

  @media (max-width: 768px) {
    font-size: 14px;
  }

  @media (max-width: 480px) {
    font-size: 14px;
  }

  &:last-child {
    border-right: none;
  }
`;

const TableCell = styled.td`
  padding: 20px 10px;
  border: 1px solid ${({ theme }) => theme.tableBorderColor};
  text-align: center;
`;

const LiveDataLink = styled.a`
  text-decoration: none;
  color: ${({ theme }) => theme.textColor};

  &:hover {
    color: ${({ theme }) => theme.linkHoverColor};
    text-decoration: underline;
  }
`;

const LiveData = () => {
  const [liveData, setLiveData] = useState(null);

  useEffect(() => {
    const fetchLiveData = async () => {
      const data = await getLiveData();
      setLiveData(data);
    };

    fetchLiveData();
  }, []);

  const buildTwitterShareLinkHEX = (data) => {
    if (data) {
      const {
        price_Pulsechain,
        tsharePrice_Pulsechain,
        tshareRateHEX_Pulsechain,
        payoutPerTshare_Pulsechain,
        penaltiesHEX_Pulsechain,
        liquidityHEX_Pulsechain,
        liquidityEHEX_Pulsechain,
      } = data;

      const tweetContent = ` Exciting news for $HEX holders! Check out the latest stats for $HEX on $PLS \n\n Price: $${formatNumber(price_Pulsechain)}\n T-Share Price: $${formatNumber(tsharePrice_Pulsechain)}\n T-Share Rate: ${formatNumber(tshareRateHEX_Pulsechain)}\n Payout per T-Share: ${formatNumber(payoutPerTshare_Pulsechain)}\n Penalties: ${formatNumber(penaltiesHEX_Pulsechain)}\n Liquidity HEX: ${formatNumber(liquidityHEX_Pulsechain)}\n Liquidity eHEX: ${formatNumber(liquidityEHEX_Pulsechain)}\n\n`;

      const encodedTweetContent = encodeURIComponent(tweetContent);
      const twitterShareLink = `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;

      return twitterShareLink;
    }
    return '';
  };

  const buildTwitterShareLinkETH = (data) => {
    if (data) {
      const {
        price,
        tsharePrice,
        tshareRateHEX,
        payoutPerTshare,
        penaltiesHEX,
        liquidityHEX,
      } = data;

      const tweetContent = ` Exciting news for $HEX holders! Check out the latest stats for $HEX on ETH \n\n Price: $${formatNumber(price)}\n T-Share Price: $${formatNumber(tsharePrice)}\n T-Share Rate: ${formatNumber(tshareRateHEX)}\n Payout per T-Share: ${formatNumber(payoutPerTshare)}\n Penalties: ${formatNumber(penaltiesHEX)}\n Liquidity HEX: ${formatNumber(liquidityHEX)}\n\n`;

      const encodedTweetContent = encodeURIComponent(tweetContent);
      const twitterShareLink = `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;

      return twitterShareLink;
    }
    return '';
  };

  const buildTwitterShareLinkPLS = (data) => {
    if (data && data.pricePLS_Pulsechain) {
      const tweetContent = ` Exciting news for $PLS holders! The current price of $PLS on PulseChain is $${(data.pricePLS_Pulsechain)} \n\nDon't miss out! #PLS #PulseChain #Crypto`;
      const encodedTweetContent = encodeURIComponent(tweetContent);
      const twitterShareLink = `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;

      return twitterShareLink;
    }
    return '';
  };

  const buildTwitterShareLinkPLSX = (data) => {
    if (data && data.pricePLSX_Pulsechain) {
      const tweetContent = ` Exciting news for $PLSX holders! The current price of $PLSX on PulseChain is $${(data.pricePLSX_Pulsechain)} \n\nDon't miss out! #PLSX #PulseChain #Crypto`;
      const encodedTweetContent = encodeURIComponent(tweetContent);
      const twitterShareLink = `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;

      return twitterShareLink;
    }
    return '';
  };

  const buildTwitterShareLinkINC = (data) => {
    if (data && data.priceINC_Pulsechain) {
      const tweetContent = ` Attention $INC enthusiasts! The current price of $INC on PulseChain is $${formatNumber(data.priceINC_Pulsechain)} \n\nJoin the conversation! #INC #PulseChain #Crypto`;
      const encodedTweetContent = encodeURIComponent(tweetContent);
      const twitterShareLink = `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;

      return twitterShareLink;
    }
    return '';
  };

  const buildTwitterShareLinkAllPrices = (data) => {
    if (data) {
      const {
        price_Pulsechain,
        price,
        pricePLS_Pulsechain,
        pricePLSX_Pulsechain,
        priceINC_Pulsechain,
      } = data;

      const tweetContent = ` Current Prices:
          $HEX: $${formatNumber(price_Pulsechain)}
          $eHEX: $${formatNumber(price)}
          $PLS: $${(pricePLS_Pulsechain)}
          $PLSX: $${(pricePLSX_Pulsechain)}
          $INC: $${formatNumber(priceINC_Pulsechain)}
          #HEX #eHEX #PLS #PLSX #INC #PulseChain #Crypto`;

      const encodedTweetContent = encodeURIComponent(tweetContent);
      return `https://twitter.com/intent/tweet?text=${encodedTweetContent}`;
    }
    return '';
  };

  const formatNumber = (number) => {
    return Number(number).toLocaleString();
  };

  return (
    <Table>
      <thead>
        <TableRow>
          <TableHeader>ASSET</TableHeader>
          <TableHeader>PRICE</TableHeader>
          <TableHeader>ACTIONS</TableHeader>
        </TableRow>
      </thead>
      <tbody>
        {liveData && (
          <>
            <TableRow>
              <TableCell>
                <img
                  src={hexplslogo}
                  alt="Description"
                  style={{ width: '23px', height: 'auto', marginRight: '8px', verticalAlign: 'middle' }}
                />
                HEX
              </TableCell>
              <TableCell>${liveData.price_Pulsechain} </TableCell>
              <TableCell>
                <LiveDataLink href={buildTwitterShareLinkHEX(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> HEX 
                </LiveDataLink>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                <img
                  src={hexethlogo}
                  alt="Description"
                  style={{ width: '23px', height: 'auto', marginRight: '8px', verticalAlign: 'middle' }}
                />
                eHEX
              </TableCell>
              <TableCell>${liveData.price} </TableCell>
              <TableCell>
                <LiveDataLink href={buildTwitterShareLinkETH(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> eHEX 
                </LiveDataLink>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                <img
                  src={plslogo}
                  alt="Description"
                  style={{ width: '23px', height: 'auto', marginRight: '8px', verticalAlign: 'middle' }}
                />
                PLS
              </TableCell>
              <TableCell>${liveData.pricePLS_Pulsechain} </TableCell>
              <TableCell>
                <LiveDataLink href={buildTwitterShareLinkPLS(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> PLS
                </LiveDataLink>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                <img
                  src={plsxlogo}
                  alt="Description"
                  style={{ width: '23px', height: 'auto', marginRight: '8px', verticalAlign: 'middle' }}
                />
                PLSX
              </TableCell>
              <TableCell>${liveData.pricePLSX_Pulsechain} </TableCell>
              <TableCell>
                <LiveDataLink href={buildTwitterShareLinkPLSX(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> PLSX
                </LiveDataLink>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                <img
                  src={inclogo}
                  alt="Description"
                  style={{ width: '23px', height: 'auto', marginRight: '8px', verticalAlign: 'middle' }}
                />
                INC
              </TableCell>
              <TableCell>${liveData.priceINC_Pulsechain.toLocaleString()} </TableCell>
              <TableCell>
                <LiveDataLink href={buildTwitterShareLinkINC(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> INC
                </LiveDataLink>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan="3" style={{ textAlign: 'center' }}>
                <LiveDataLink href={buildTwitterShareLinkAllPrices(liveData)} target="_blank" rel="noopener noreferrer">
                  <FontAwesomeIcon icon={faTwitter} /> ALL PRICES
                </LiveDataLink>
              </TableCell>
            </TableRow>
          </>
        )}
      </tbody>
    </Table>
  );
};

export default LiveData;
