import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { getLiveData } from './Api';
import logo from './images/plshex.png';

const glow = keyframes`
  0% {
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 123, 255, 0.7);
  }
  100% {
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
  }
`;

const ProgressBarContainer = styled.div`
  width: 100%;
  height: 50px;
  background-color: ${({ theme }) => theme.bodyBg};
 
  overflow: hidden;
  box-shadow: ${({ theme }) => theme.boxShadow};
  display: flex;
  align-items: center;
  position: relative;

  @media (max-width: 768px) {
    height: 40px;
  }

  @media (max-width: 480px) {
    height: 40px;
  }
`;

const ProgressIndicator = styled.div`
  height: 100%;
  background-color: ${({ theme }) => theme.progressIndicatorColor};
  position: absolute;
  top: 0;
  left: 0;
  transition: width 0.5s ease-in-out;
  animation: ${glow} 2s infinite alternate;
`;

const Text = styled.p`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  color: ${({ theme }) => theme.textColor};
  font-weight: bold;
  margin: 0;
  text-shadow: ${({ theme }) => theme.textShadow};
  background-color: ${({ theme }) => theme.bodyBg};
  border-radius: 5px;

  @media (max-width: 768px) {
    font-size: 14px;
  }

  @media (max-width: 480px) {
    font-size: 14px;
  }
`;

const Logo = styled.img`
  position: absolute;
  left: 10px;
  height: 40px;
  z-index: 1000;

  @media (max-width: 768px) {
    left: 5px;
    height: 30px;
  }

  @media (max-width: 480px) {
    left: 3px;
    height: 20px;
  }
`;

const ProgressBar = () => {
  const [hexPrice, setHexPrice] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchHexPrice = async () => {
      try {
        const data = await getLiveData();
        setHexPrice(data.price_Pulsechain);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching PULSECHAIN HEX price:', error);
        setLoading(false);
      }
    };

    const interval = setInterval(fetchHexPrice, 60000);
    fetchHexPrice();

    return () => clearInterval(interval);
  }, []);

  if (loading || hexPrice === null) {
    return <div>Loading...</div>;
  }

  const centProgress = hexPrice % 0.01;
  const progress = (centProgress / 0.01) * 100;

  return (
    <ProgressBarContainer>
      <Logo src={logo} alt="Logo" />
      <ProgressIndicator style={{ width: `${progress}%` }} />
      <Text>{` $${hexPrice.toFixed(5)}`}</Text>
    </ProgressBarContainer>
  );
};

export default ProgressBar;
