import React, { useState, useEffect } from 'react';
import { jsPDF } from 'jspdf';
import ThemeToggle from './ThemeToggle';
import placeholderLogo from './images/placeholderlogo.png';
import QRCode from 'qrcode';

import {
  Container,
  Form,
  FormRow,
  FormLabel,
  Input,
  Button,
  ServicesList,
  ServiceItem,
  TotalAmount,
  InputContainer,
  LogoContainer,
  CompanyLogo,
  CompanyDetails,
  CompanyDetailsInput,
  Title,
  RemoveButton,
  FileInputContainer,
  FileInput,
  FileInputLabel,
  CurrencySelector,
} from './StyledComponents'; // Update the path as per your file structure

const InvoiceGenerator = ({ toggleTheme }) => {
  const [companyLogo, setCompanyLogo] = useState(placeholderLogo); // Initialize with default photo URL
  const [companyDetails, setCompanyDetails] = useState({ name: '', address: '', contact: '' });
  const [clientName, setClientName] = useState('');
  const [serviceName, setServiceName] = useState('');
  const [quantity, setQuantity] = useState(0);
  const [rate, setRate] = useState(0);
  const [services, setServices] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0);
  const [currency, setCurrency] = useState('USD'); // State for selected currency
  const [isLoading, setIsLoading] = useState(false);
  const [feedbackMessage, setFeedbackMessage] = useState({ type: '', text: '' });
  const [shippingInput, setShippingInput] = useState('');
  const [salesTaxInput, setSalesTaxInput] = useState('');
  const [discountInput, setDiscountInput] = useState('');
  const [additionalNotes, setAdditionalNotes] = useState('');

  const commonClients = [
    "Company 1",
    "Company 2",
    "Company 3",
    "Company X",
    "Company Y",
    "Company Z"
  ];

  // Function to calculate total amount
  const calculateTotalAmount = () => {
    let total = 0;
    services.forEach(service => {
      total += service.total;
    });
    setTotalAmount(total);
  };

  // useEffect to recalculate total amount whenever services or their properties change
  useEffect(() => {
    calculateTotalAmount();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [services]);

  // Function to handle currency change
  const handleCurrencyChange = (selectedCurrency) => {
    setCurrency(selectedCurrency);
  };

  const handleButtonClick = (value, type) => {
    if (type === 'quantity') {
      setQuantity(quantity + value);
    } else if (type === 'rate') {
      setRate(rate + value);
    }
  };

  const addService = () => {
    setIsLoading(true);
    const serviceTotal = quantity * rate;
    const newService = {
      name: serviceName,
      quantity: quantity,
      rate: rate,
      total: serviceTotal,
    };
    setServices([...services, newService]);
    setServiceName('');
    setQuantity(0);
    setRate(0);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Service added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const generateInvoice = async () => {
    setIsLoading(true);

    if (!clientName || services.length === 0) {
      setIsLoading(false);
      setFeedbackMessage({
        type: 'error',
        text: 'Please enter the client name and at least one service.'
      });
      setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);
      return;
    }

    const doc = new jsPDF({
      orientation: 'p',
      unit: 'mm',
      format: 'a4'
    });

    // Add logo and company details
    if (companyLogo) {
      doc.addImage(companyLogo, 'PNG', 15, 10, 30, 30); // Adjust logo position and size
    }
    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(10);
    doc.setTextColor(47, 79, 79); // Set text color to a dark shade
    doc.text(`${companyDetails.name}`, 15, 55); // Adjust company details position
    doc.text(`${companyDetails.address}`, 15, 60); // Adjust company details position
    doc.text(`${companyDetails.contact}`, 15, 65); // Adjust company details position

    // Invoice Title
    doc.setFont('Helvetica', 'bold');
    doc.setFontSize(24); // Larger font size
    doc.setTextColor(0, 0, 0); // Set text color back to black
    doc.text('Invoice', 190, 20, null, null, 'right'); // Positioned in the top right-hand corner

    // Invoice Information
    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(12); // Increase font size for clarity
    doc.text(`Date: ${new Date().toLocaleDateString()}`, 15, 85); // Adjust position
    doc.text(`Client: ${clientName}`, 15, 95); // Adjust position

    // Service Details
    let yPos = 105; // Adjust starting position

    // Draw Table Headers
    doc.setFillColor(211, 211, 211); // Set header background color
    doc.setFont('Helvetica', 'bold');
    doc.setFontSize(12);
    doc.rect(15, yPos, 180, 10, 'F'); // Draw table header rectangle
    doc.text('Service Name', 20, yPos + 8); // Service Name column header
    doc.text('Quantity', 80, yPos + 8); // Quantity column header
    doc.text('Rate', 120, yPos + 8); // Rate column header
    doc.text('Total', 160, yPos + 8); // Total column header

    // Draw Table Rows
    yPos += 10; // Move to the first row
    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(10);
    services.forEach((service, index) => {
      doc.rect(15, yPos, 180, 7); // Draw row border
      doc.text(service.name, 20, yPos + 5); // Service Name
      doc.text(service.quantity.toString(), 80, yPos + 5, null, null, 'right'); // Quantity
      doc.text(`${currencySymbols[currency]}${service.rate.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 120, yPos + 5, null, null, 'right'); // Rate
      doc.text(`${currencySymbols[currency]}${service.total.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 160, yPos + 5, null, null, 'right'); // Total
      yPos += 7; // Move to the next row
    });

    // Total Amount
    yPos += 10; // Adjust spacing
    doc.setFont('Helvetica', 'bold');
    doc.text(`Total Amount: ${currencySymbols[currency]}${totalAmount.toLocaleString('en-US', { maximumFractionDigits: 2 })}`, 15, yPos);

    // Footer: Additional Notes, Terms, or Payment Details
    doc.setFont('Helvetica', 'normal');
    doc.setFontSize(10);
    doc.text('Additional Notes:', 15, 250); // Adjust position
    doc.setFont('Helvetica', 'italic'); // Set font style to italic
    doc.text(additionalNotes, 15, 255); // Display additional notes

    // Add QR Code
    try {
      const qrCodeDataURL = await QRCode.toDataURL('https://hex.com/');
      doc.addImage(qrCodeDataURL, 'PNG', 150, 220, 40, 40); // Adjust position and size of QR code
    } catch (error) {
      console.error('Error generating QR code:', error);
    }

    // Update company details and feedback
    updateCompanyDetails(companyDetails);
    setIsLoading(false);
    setFeedbackMessage({
      type: 'success',
      text: 'Invoice generated successfully.'
    });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);

    // Save the generated invoice as PDF
    doc.save('invoice.pdf');
  };

  const clearForm = () => {
    setClientName('');
    setServices([]);
    setTotalAmount(0);
    setCompanyLogo(null); // Clear the loaded logo image
    setFeedbackMessage({ type: 'success', text: 'Form cleared successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const addSalesTax = (taxRate) => {
    setIsLoading(true);
    const taxAmount = totalAmount * taxRate;
    const newService = {
      name: "Sales Tax",
      quantity: 1,
      rate: taxRate * 100,
      total: taxAmount,
    };
    setServices([...services, newService]);
    setTotalAmount(totalAmount + taxAmount);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Sales tax added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const addShipping = (shippingCost) => {
    setIsLoading(true);
    const newService = {
      name: "Shipping",
      quantity: 1,
      rate: shippingCost,
      total: shippingCost,
    };
    setServices([...services, newService]);
    setTotalAmount(totalAmount + shippingCost);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Shipping added successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const applyDiscount = (amount) => {
    setIsLoading(true);
    setTotalAmount(totalAmount - amount);
    const newService = {
      name: `Discount (-$${amount})`,
      quantity: 1,
      rate: -amount,
      total: -amount,
    };
    setServices([...services, newService]);
    setIsLoading(false);
    setFeedbackMessage({ type: 'success', text: 'Discount applied successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000); // Clear feedback message after 3 seconds
  };

  const removeService = (index) => {
    const updatedServices = services.filter((_, i) => i !== index);
    const updatedTotalAmount = updatedServices.reduce((acc, curr) => acc + curr.total, 0);
    setServices(updatedServices);
    setTotalAmount(updatedTotalAmount);
    setFeedbackMessage({ type: 'success', text: 'Service removed successfully.' });
    setTimeout(() => setFeedbackMessage({ type: '', text: '' }), 3000);
  };

  const quantityButtons = [1, 5, 10, 25, 100];
  const rateButtons = [1, 5, 10, 20, 100];

  // Load company details from local storage when the component mounts
  useEffect(() => {
    const storedCompanyDetails = localStorage.getItem('companyDetails');
    if (storedCompanyDetails) {
      setCompanyDetails(JSON.parse(storedCompanyDetails));
    }
  }, []); // Empty dependency array ensures this effect runs only once on mount

  // Handle logo change
  const handleLogoChange = (e) => {
    if (e.target.files.length > 0) {
      setCompanyLogo(URL.createObjectURL(e.target.files[0]));
    } else {
      // If no file is selected, set the company logo to the placeholder image
      setCompanyLogo(placeholderLogo);
    }
  };

  // Update company details and save to local storage
  const updateCompanyDetails = (newDetails) => {
    setCompanyDetails(newDetails);
    localStorage.setItem('companyDetails', JSON.stringify(newDetails));
  };

  const currencySymbols = {
    USD: '$',
    EUR: '',
    AUD: '$',
    CAD: '$'
  };

  // Handle company details change
  const handleCompanyDetailsChange = (field, value) => {
    setCompanyDetails({ ...companyDetails, [field]: value });
  };

  return (
    <Container>
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <Title style={{ margin: '0 20px 0 0', }}>Create Invoice</Title>
        <div>
          <ThemeToggle toggleTheme={toggleTheme} />
        </div>
      </div>
      <LogoContainer>
        <CompanyLogo src={companyLogo} alt="Company Logo" />
        <CompanyDetails>
          <CompanyDetailsInput
            type="text"
            value={companyDetails.name}
            onChange={(e) => handleCompanyDetailsChange('name', e.target.value)}
            placeholder="Enter company name"
          />
          <CompanyDetailsInput
            type="text"
            value={companyDetails.address}
            onChange={(e) => handleCompanyDetailsChange('address', e.target.value)}
            placeholder="Enter company address"
          />
          <CompanyDetailsInput
            type="text"
            value={companyDetails.contact}
            onChange={(e) => handleCompanyDetailsChange('contact', e.target.value)}
            placeholder="Enter company contact info"
          />
        </CompanyDetails>
        <FileInputContainer>
          <FileInputLabel htmlFor="company-logo-upload">Upload Company Logo</FileInputLabel>
          <FileInput
            id="company-logo-upload"
            type="file"
            accept="image/*"
            onChange={handleLogoChange}
          />
        </FileInputContainer>
      </LogoContainer>
      <Form onSubmit={(e) => e.preventDefault()}>
        <FormRow>
          <FormLabel>Client Name:</FormLabel>
          <Input
            type="text"
            value={clientName}
            onChange={(e) => setClientName(e.target.value)}
            placeholder="Enter client name"
          />
        </FormRow>
        <FormRow>
          <InputContainer>
            {commonClients.map((client, index) => (
              <Button key={index} onClick={() => setClientName(client)}>
                {client}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Service Name:</FormLabel>
          <Input
            type="text"
            value={serviceName}
            onChange={(e) => setServiceName(e.target.value)}
            placeholder="Enter service name"
          />
        </FormRow>
        <FormRow>
          <InputContainer>
            <Button onClick={() => setServiceName("Product Sale")}>Product Sale</Button>
            <Button onClick={() => setServiceName("Retail")}>Retail</Button>
            <Button onClick={() => setServiceName("Consultation")}>Consultation</Button>
            <Button onClick={() => setServiceName("Repair Service")}>Repair Service</Button>
            <Button onClick={() => setServiceName("Delivery")}>Delivery</Button>
            <Button onClick={() => setServiceName("Installation")}>Installation</Button>
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Quantity:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={quantity}
              onChange={(e) => setQuantity(parseInt(e.target.value))}
              placeholder="Enter quantity"
            />
            {quantityButtons.map((btnValue, index) => (
              <Button key={index} onClick={() => handleButtonClick(btnValue, 'quantity')}>
                +{btnValue}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Rate ($):</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={rate}
              onChange={(e) => setRate(parseInt(e.target.value))}
              placeholder="Enter rate"
            />
            {rateButtons.map((btnValue, index) => (
              <Button key={index} onClick={() => handleButtonClick(btnValue, 'rate')}>
                +${btnValue}
              </Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Additional Notes:</FormLabel>
          <Input
            type="text"
            value={additionalNotes}
            onChange={(e) => setAdditionalNotes(e.target.value)}
            placeholder="e.g. Payment due within 30 days."
          />
        </FormRow>
        <FormRow>
          <Button onClick={addService}>Add to Invoice</Button>
          <Button onClick={clearForm}>Start Over</Button>
        </FormRow>
        <FormRow>
          <ServicesList>
            {services.map((service, index) => (
              <ServiceItem key={index}>
                {service.name} - Quantity: {service.quantity}, Rate: {currencySymbols[currency]}{service.rate} {currency}, Total: {currencySymbols[currency]}{service.total} {currency}
                <RemoveButton onClick={() => removeService(index)}>Remove</RemoveButton>
              </ServiceItem>
            ))}
          </ServicesList>
          <TotalAmount>Total Amount: {currencySymbols[currency]}{(totalAmount.toFixed(2)).toLocaleString()} {currency}</TotalAmount>
          {isLoading && <div>Loading...</div>}
          {feedbackMessage.type === 'success' && <div style={{ color: 'green' }}>{feedbackMessage.text}</div>}
          {feedbackMessage.type === 'error' && <div style={{ color: 'red' }}>{feedbackMessage.text}</div>}
          <Button onClick={generateInvoice}>Download PDF</Button>
        </FormRow>
        <FormRow>
          <FormLabel>Select Currency:</FormLabel>
          <CurrencySelector value={currency} onChange={(e) => handleCurrencyChange(e.target.value)}>
            <option value="USD">USD</option>
            <option value="EUR">EUR</option>
            <option value="AUD">AUD</option> {/* Add AUD option */}
            <option value="CAD">CAD</option> {/* Add CAD option */}
          </CurrencySelector>
        </FormRow>
        <FormRow>
          <FormLabel>Add Shipping:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={shippingInput}
              onChange={(e) => setShippingInput(e.target.value)}
              placeholder="Enter shipping amount"
            />
            <Button onClick={() => addShipping(parseFloat(shippingInput))} style={{ width: 'auto' }}>
              Add
            </Button>
            {/* Quick buttons for common shipping costs */}
            {[5, 10, 15, 20].map((shippingCost, index) => (
              <Button key={index} onClick={() => addShipping(shippingCost)}>${shippingCost}</Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Add Sales Tax:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={salesTaxInput}
              onChange={(e) => setSalesTaxInput(e.target.value)}
              placeholder="Enter sales tax rate"
            />
            <Button onClick={() => addSalesTax(parseFloat(salesTaxInput) / 100)} style={{ width: 'auto' }}>
              Add
            </Button>
            {/* Quick buttons for common sales tax rates */}
            {[5, 7, 8.875, 9, 10].map((taxRate, index) => (
              <Button key={index} onClick={() => addSalesTax(taxRate / 100)}>{taxRate}%</Button>
            ))}
          </InputContainer>
        </FormRow>
        <FormRow>
          <FormLabel>Apply Discount:</FormLabel>
          <InputContainer>
            <Input
              type="number"
              value={discountInput}
              onChange={(e) => setDiscountInput(e.target.value)}
              placeholder="Enter discount amount"
            />
            <Button onClick={() => applyDiscount(parseFloat(discountInput))} style={{ width: 'auto' }}>
              Apply
            </Button>
            {/* Quick buttons for common discount amounts */}
            {[5, 10, 15, 20].map((discountAmount, index) => (
              <Button key={index} onClick={() => applyDiscount(discountAmount)}>-${discountAmount}</Button>
            ))}
          </InputContainer>
        </FormRow>
      </Form>
    </Container>
  );
};

export default InvoiceGenerator;
