import React, { useState, useEffect } from 'react';
import styled, { withTheme } from 'styled-components';

const NavbarContainer = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px; /* Adjusted padding for better spacing */

  color: ${({ theme }) => theme.navTextColor};
  width: 100%;

`;

const UTCClockContainer = styled.div`
  font-size: 16px;
  @media (max-width: 768px) {
    font-size: 14px;
  }

  @media (max-width: 480px) {
    font-size: 12px;
  }
`;

const Divider = styled.span`
  margin: 0 10px;
  color: ${({ theme }) => theme.textColor};
`;

const UTCClock = ({ theme }) => {
  const [clockInfo, setClockInfo] = useState({ hexDay: '', date: '', utcTime: '' });

  useEffect(() => {
    const updateClock = () => {
      const now = new Date();
      const utcString = now.toUTCString();

      const hexStartDate = new Date('2019-12-02T00:00:00Z');
      const hexDay = Math.floor((now - hexStartDate) / (1000 * 60 * 60 * 24)).toString();

      setClockInfo({
        hexDay: `Day ${hexDay}`,
        date: now.toLocaleDateString('en-US', { day: 'numeric', month: 'short', year: 'numeric' }).toUpperCase(),
        utcTime: utcString.slice(17, -4),
      });
    };

    const intervalId = setInterval(updateClock, 1000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <NavbarContainer theme={theme}>
      <UTCClockContainer>
        <span>{clockInfo.hexDay}</span>
        <Divider>|</Divider>
        <span>{clockInfo.date}</span>
        <Divider>|</Divider>
        <span>{clockInfo.utcTime} UTC</span>
      </UTCClockContainer>
    </NavbarContainer>
  );
};

export default withTheme(UTCClock);
